import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import EnterCardNo from '@/components/enterCardNo'   //输入银行卡号
import AddBankCard from '@/components/addBankCard'  //添加银行卡
import BindCardSuccess from '@/components/bindCardSuccess'   //绑卡成功
import BindCardFailed from '@/components/bindCardFailed'   //绑卡失败
import QueryPage from '@/components/queryPage'   //绑卡失败
import VerifyInfo from '@/components/verifyInfo'  //验证码

Vue.use(Router)
Vue.use(Resource)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'EnterCardNo',   //输入银行卡号
      component: EnterCardNo
    },
    {
      path: '/AddBankCard',  //添加银行卡组件
      name: 'AddBankCard',
      component: AddBankCard
    },
    {
      path: '/BindCardSuccess',  //绑卡成功
      name: 'BindCardSuccess',
      component: BindCardSuccess
    },
    {
      path: '/BindCardFailed',  //绑卡失败
      name: 'BindCardFailed',
      component: BindCardFailed
    },
    {
      path: '/QueryPage',  //查询页
      name: 'QueryPage',
      component: QueryPage
    },
    {
      path: '/VerifyInfo',  //信息验证
      name: 'VerifyInfo',
      component: VerifyInfo
    } 
  ]
})
export default router;



