<template>
  <div class="queryPage">
    <span class="icon result">&#xe612;</span>
    <p class="mt20 c333 fs20">绑卡请求超时！</p>
    <p class="mt20 c333 fs14">请查询绑卡结果或重新进行绑定</p>
    <div class="pl12 pr12">
      <button class="mt30 w" id="query" @click="searchResult" type="button">查询结果</button>
    </div>
    <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
  </div>
</template>

<script>
let ERR_OK = 'success';
import BindCardSuccess from '@/components/bindCardSuccess'
import BindCardFailed from '@/components/bindCardFailed'
export default {
  name: 'queryPage',
  data () {
    return {
      nopOrderId:'',
      requestFlowId:'',
      isShowMask: false
    }
  },
  created() {
    var self = this;
    self.nopOrderId = localStorage.nopOrderId;
    self.requestFlowId = localStorage.requestFlowId;
    if(self.nopOrderId == '' || self.nopOrderId == 'undefined'){
      alert('NOP订单号获取失败');
      return false;
    }
    if(self.requestFlowId == '' || self.requestFlowId == 'undefined'){
      alert('NOP流水号获取失败');
      return false;
    }
  },
  methods:{
    searchResult() {
      let self = this;
      var param = bindCardMerchantRequest; 
      param.token = token;
      param.nopOrderId = self.nopOrderId
      param.requestFlowId = self.requestFlowId;
      self.$nextTick(function(){
        User.form.disabledSubmit("#query");
        self.$http.get(Request.url+'/bindCard/queryBindCardOrder',{params:param}).then((res) => {
          User.form.enabledSubmit("#query");
          var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
          if(data.orderStatus == 'bindCardSuccess'){  //绑卡成功
            this.$router.push({name:'BindCardSuccess', path: '/BindCardSuccess', component:BindCardSuccess})
          }else if(data.orderStatus == 'bindCardFail'){  //绑卡失败
            this.$router.push({name:'BindCardFailed', path: '/BindCardFailed', component:BindCardFailed})
          }else if(data.orderStatus == 'bindCardKnown'){ //处理中
            self.isShowMask = true;
            Tip.titleContentBtn('绑卡正在处理中',function(){
              dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                self.isShowMask = false;
                dom.querySelector("#tip1").classList.add("none");
              },false);
            })
          }
        });
      });
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.queryPage{
  padding-top: 50px;
  text-align: center;
  .result{
    color:#ff722d;
    font-size:54px;
  }
}
</style>



