<template>
  <div class="enterCardNo">
      <div v-if="!errorcode">
        <p class="mt10 ca9a9a9 h42 fs12 pl12 pr12">请按本人银行卡信息进行操作</p>
        <form class="bgfff" id="form" @submit="noSubmit($event)">
          <label>
            <div class="li pr pl12 pr12 fs16">
                <input class="required bankCard input-text w93" name="cardno" _name="cardno" type="number" placeholder="请输入银行卡号" v-model="cardNo">
                <i class="del pa icon" style="top:10px;" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
        </form>
        <div class="pl12 pr12" >
          <button class="w mt30" id="submit" type="button" @click="toNext">下一步</button>
        </div>
        <div v-if="this.token">
          <p class="c2eb944 fs14 pl12 mt10" @click="supportBank">支持银行与限额</p>
        </div>
        <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
      </div>
      <div class=" tc frontFail" v-if="errorcode">
        <span class="icon failed">&#xe65d;</span>
        <p class="fs18 mt10">{{errormsg}}</p>
      </div>
       <div v-if="showActivity" style="margin-top: 100px"><img style="max-width: 100%" :src='this.contextPath+"/static/images/paji_card_20180416.jpg"'></div>
  </div>
</template>

<script>

let ERR_OK = 'success'; //返回成功码
import AddBankCard from '../components/addBankCard.vue'
export default {
  name: 'enterCardNo',
  data () {
    return {
      errorcode:errorcode,
      errormsg:errormsg,
      cardNo:'',
      addItem:{},
      isShowMask:false ,//mask遮罩
      token:token,
      contextPath:contextPath,
      showActivity:showActivity
    }
  },
  watch:{
    cardNo(){
      if(this.cardNo.length > 23){
        this.cardNo = this.cardNo.substring(0,23);
      }
    }
  },
  mounted() { 
    if(document.querySelector("#form")){
      User.form.validate("#form");
    }
  },
  methods:{
    noSubmit(event){
      event.preventDefault();
      return false;
    },
    toNext() {
      var self = this;
      var result = User.form.autoValidate("#form");
      if (result) {
        User.form.disabledSubmit("#submit");
        self.$http.get(Request.url+'/bindCard/binInfo',{params: {cardno:BASE64.encoder(this.cardNo),'':bindCardMerchantRequest,'token':token}}).then((res) => {
          var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
          User.form.enabledSubmit("#submit");
          if(data.bizStatus == ERR_OK){
            localStorage.setItem("bindCardInfo",JSON.stringify(data.bindCardInfo));
            self.addItem.cardno= BASE64.encoder(self.cardNo);
            localStorage.setItem("addItem",JSON.stringify(self.addItem));
            self.$router.push({ name: 'AddBankCard',path:'/AddBankCard',component: AddBankCard })
          }else{
            self.isShowMask = true;
            Tip.titleContentBtn('提示',data.errormsg,function(){
              dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                self.isShowMask = false;
                dom.querySelector("#tip1").classList.add("none");
              },false);
            })
          }
        });
      }
    },
    supportBank(){  //银行卡限额列表
      window.location.href = this.contextPath+"/newwap/toSupportBankPage?token="+this.token
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.h42{line-height: 42px;}
.w93{width:93%;}
.bankCard{height:32px;line-height: 32px;padding:6px;}
.frontFail{
  padding-top: 50px;
  text-align: center;
  .failed{
    color:#ff722d;
    font-size:58px;
  }
}
label{display: block;height:44px;}
</style>



