    <!-- 服务协议 -->
    <div class="pme-popup pme-protocol scroll" :class="{show:protocol}"><h2>绑卡支付用户服务协议<span class="close" @click="close">同意</span></h2><div class="content-wrapper" tabindex="-1" style="height: 571px;"><div class="content"><p>一、概述</p><p>1、易宝支付《绑卡支付用户服务协议》（以下简称“本协议”）是您自愿与易宝支付有限公司（以下简称“易宝支付”）就易宝绑卡支付服务（以下简称“本服务”）的使用所签订的有效合约。您通过网络页面点击确认本协议或以其他方式选择接受本协议，即表示您与易宝支付已达成协议并同意接受本协议的全部约定内容。</p><p>2、在您接受本协议之前，请您仔细阅读本协议的全部内容，如您不同意接受本协议的任意内容，或者无法准确理解相关条款含义的，请不要进行后续操作。如果您对本协议的条款有疑问的，请通过易宝支付客服渠道进行询问（易宝支付客服电话为4001-500-800），易宝支付将向您解释条款内容。</p><p>3、您同意，易宝支付有权随时对本协议内容进行单方面的变更，无需另行单独通知您；若您在本协议内容公告变更生效后继续使用本服务的，表示您已充分阅读、理解并接受变更修改后的协议内容，也将遵循变更修改后的协议内容使用本服务；若您不同意变更修改后的协议内容，您应在变更生效前停止使用本服务。</p><p>二、双方权利义务</p><p>1、您应确保您在使用本服务时的银行卡为您本人的银行卡，确保您使用银行卡的行为合法、有效，未侵犯任何第三方合法权益；否则因此造成易宝支付、商户、持卡人损失的，您应负责赔偿并承担全部法律责任。</p><p>2、您应确保开通本服务所提供的手机号码为本人所有，并授权易宝支付可以通过第三方渠道对您所提供手机号码的真实性、有效性进行核实。</p><p>您应妥善保管银行卡、卡号、密码、发卡行预留的手机号码等与银行卡有关的一切信息。如您遗失或泄漏前述信息，您应及时通知发卡行及/或易宝支付，以减少可能发生的损失。无论是否已通知发卡行及/或易宝支付，因您的原因所致损失需由您自行承担。</p><p>3、您应妥善保管银行卡、卡号、密码、发卡行预留的手机号码等与银行卡有关的一切信息。如您遗失或泄漏前述信息，您应及时通知发卡行及/或易宝支付，以减少可能发生的损失。无论是否已通知发卡行及/或易宝支付，因您的原因所致损失需由您自行承担。</p><p>4、您在使用本服务时，应当认真确认交易信息，包括且不限于商品名称、数量、金额等，并按易宝支付业务流程发出约定的指令。您认可和同意：您发出的指令不可撤回或撤销，易宝支付一旦根据您的指令委托银行或第三方从银行卡中划扣资金给收款人，您不应以非本人意愿交易或其他任何原因要求易宝支付退款或承担其他责任。</p><p>5、您不应将本服务用于任何违反国家相关法律法规或本协议的目的。</p><p>6、您在对使用本服务过程中发出指令的真实性及有效性承担全部责任；您承诺，易宝支付依照您的指令进行操作的一切风险由您承担。</p><p>7、您认可并以易宝支付系统平台记录的数据为准。</p><p>8、同时您授权易宝支付有权留存您在易宝支付网站填写的相应信息，以供后续向您持续性地提供相应服务（包括但不限于将本信息用于向您推广、提供其他更加优质的产品或服务）。</p><p>9、出现下列情况之一的，易宝支付有权立即终止您使用易宝支付相关服务而无需承担任何责任：</p><p>&nbsp;（1）违反本协议的约定；</p><p>&nbsp;（2）将本服务用于非法目的；</p><p>&nbsp;（3）违反易宝支付/或其他关联公司网站的条款、协议、规则、通告等相关规定，而被上述任一网站终止提供服务的；</p><p>&nbsp;（4）易宝支付认为向您提供本服务存在风险的；</p><p>&nbsp;（5）您的银行卡无效、有效期届满或注销（如有）。</p><p>10、若您未违反本协议约定且因不能归责于您的原因，造成银行卡内资金通过本服务出现损失，且您未从中获益的，您可向易宝支付申请补偿。您应在知悉资金发生损失后及时通知易宝支付并按要求提交相关的申请材料和证明文件，您同意，您能否得到补偿及具体金额取决于易宝支付自身独立的判断，易宝支付承诺不会因此损害您的合法权益。</p><p>11、您同意并认可易宝支付最终的补偿行为并不代表前述资金损失应归责于易宝支付，亦不代表易宝支付须为此承担其他任何责任。您同意，易宝支付在向您支付补偿的同时，即刻取得您可能或确实存在的就前述资金损失而产生的对第三方的所有债权及其他权利，包括但不限于就上述债权向第三方追偿的权利，且您不再就上述已经让渡给易宝支付的债权向该第三方主张任何权利，亦不再就资金损失向易宝支付主张任何权利。</p><p>12、此外，在接受补偿的同时或之后，您从其它渠道挽回了前述资金损失的，或有新证据证明您涉嫌欺诈的，或者发生您应当自行承担责任的其他情形，您应在第一时间返还易宝支付向您支付的补偿款项，否则易宝支付有权向您进行追偿。同时您承诺：资金损失事实真实存在，赔偿申请材料真实、有效。您已充分知晓并认识到，基于虚假信息申请保险赔偿将涉及刑事犯罪，易宝支付有权向国家有关机构申请刑事立案侦查。</p><p>三、承诺与保证</p><p>1、您保证使用本服务过程中提供的如姓名、身份证号、银行卡卡号及银行预留手机号等信息真实、准确、完整、有效。易宝支付按照您设置的相关信息为您提供相应服务，对于因您提供信息不真实或不完整所造成的损失由您自行承担。</p><p>2、您授权易宝支付在您使用本服务期间或本服务终止后，有权保留您在使用本服务期间所形成的相关信息数据，同时该授权不可撤销。</p><p>四、其他条款</p><p>1、您同意，本协议适用中华人民共和国大陆地区法律。因易宝支付与您就本协议的签订、履行或解释发生争议，双方应努力友好协商解决。如协商不成，易宝支付和用户同意由易宝支付住所地法院管辖审理双方的纠纷或争议。</p><p>2、本协议内容包括协议正文及所有易宝支付已经发布的或将来可能发布的易宝支付服务的使用规则。所有规则为本协议不可分割的一部分，与协议正文具有相同法律效力。若您在本协议内容发生修订后，继续使用本服务的，则视为您同意最新修订的协议内容；否则您须立即停止使用本服务。</p><p>3、您同意，本协议之效力、解释、变更、执行与争议解决均适用中华人民共和国法律，没有相关法律规定的，参照通用国际商业惯例和（或）行业惯例。</p><p>4、本协议部分内容被有管辖权的法院认定为违法或无效的，不因此影响其他内容的效力。</p><p>5、本协议未尽事宜，您需遵守易宝支付网站上公布的相关服务协议及相关规则。</p></div></div></div>
    <!-- 中国银行协议 -->
    <div class="pme-popup pme-protocol scroll" :class="{show:bankprotocol}"><h2>服务协议<span class="close" @click="closeBank">同意</span></h2><div class="content-wrapper" style="height: 571px;"><div class=content>请客户认真阅读本协议文本。</p>
            <p>1.中国银行股份有限公司借记卡快捷支付业务（以下简称“快捷支付业务”）是中国银行股份有限公司（以下简称“中国银行”）和易宝支付有限公司  公司（下称“合作支付机构”）联合为中国银行借记卡持卡客户（以下简称“客户”）提供的一项电子支付服务。凡是持有中国银行股份有限公司在中国大陆地区发行的借记卡的客户，均可申请该业务。</p>
            <p>2.客户在签约快捷支付业务时，将收到中国银行发送的带有手机交易码的签约确认短信。客户应认真阅读本协议，在同意本协议约定内容的前提下输入手机交易码。客户输入手机交易码即视同为同意本协议内容。</p>
            <p>3.客户通过互联网（Internet）在合作支付机构网站或客户端，选择在线与中国银行签订本服务协议，将指定的借记卡与此时登录的账户进行关联，开通快捷支付功能，并承担由此产生的一切责任。</p>
            <p>4.客户同意，中国银行以合作支付机构传送至中国银行的户名（如与合作支付机构约定不校验姓名则删除）、卡号、手机号码、证件号码（如与合作支付机构约定不校验证件号码则删除）等身份要素作为确认客户身份、为客户开通快捷支付业务的唯一证据。中国银行比对户名（如与合作支付机构约定不校验姓名则删除）、卡号、手机号码、证件号码（如与合作支付机构约定不校验证件号码则删除）与客户在银行预留信息一致后，通过95566发送手机交易码至客户预留手机号上，并对合作支付机构传送的客户填写的手机交易码进行验证，验证一致通过后，完成客户指定的借记卡与此时所登录账户的关联。客户须保证在中国银行预留手机号码为本人手机号码并确保其准确性。基于提高交易安全的目的，中国银行有权变更上述身份要素。客户须妥善保管本人中国银行借记卡信息，本人身份信息、手机交易码、安全认证工具等敏感信息，不得将敏感信息向他人透露。因客户银行预留信息错误，或个人信息、手机等保管不善产生的风险及损失由客户本人承担。</p>
            <p>5.开通快捷支付业务后，客户同意并授权中国银行在本协议约定的服务范围内，将其所关联借记卡的原有支付验证方式变更为按照合作支付机构发送的交易指令从客户指定的借记卡账户中支付相应款项。客户需在其绑定的借记卡的余额内进行支付，否则中国银行有权拒绝执行交易指令。</p>
            <p>6.中国银行可根据业务发展需要，设置或修改支付限额，客户对此无异议，客户办理快捷支付业务时需同时受中国银行和合作支付机构设置的支付限额的约束。在任何情况下，支付金额不应超过中国银行或合作支付机构设置的支付限额（以较低者为准）。如实际支付金额大于规定的支付限额，中国银行将拒绝执行交易指令。</p>
            <p>7.本协议项下，中国银行仅提供通过合作支付机构发生的支付结算服务。客户因通过合作支付机构购买商品而产生的一切关于商品或服务质量、商品交付，交易款项扣收与划付的争议均由客户与商户或商品/服务的实际销售商自行协商解决。</p>
            <p>8.客户使用借记卡快捷支付业务应防范的风险包括但不限于：</p>
            <p>（1）客户借记卡快捷支付使用的用户名、账户、密码等重要信息可能被其他人猜出、偷窥或通过木马病毒、假冒网站、欺诈电话或短信等手段窃取，从而导致客户信息泄露、资金损失、账户被恶意操作等后果。</p>
            <p>（2）手机可能被盗或未经许可被他人使用，若手机银行密码同时被窃取，可能造成账户资金损失等情况；若客户更换手机号时未取消以原手机号开通的短信提醒服务，当原手机号被他人使用时，可能造成客户的账户信息泄露等情况。</p>
            （3）客户使用设备的操作系统、杀毒软件、网络防火墙等客户端软件未及时更新或安装补丁程序，可能导致客户信息泄露、资金损失、账户被恶意操作等后果。</p>
            <p>9.客户应采取风险防范措施，安全使用借记卡快捷支付业务，相关措施包括不限于：</p>
            <p>（1）妥善保护借记卡快捷支付业务使用的用户名、账户、密码等重要身份识别标识、身份认证信息及工具，确保上述重要信息和认证工具不经口头或其它方式发生泄露或被盗用。</p>
            <p>（2）对所使用设备的操作系统、杀毒软件、网络防火墙等客户端软件采取有效的措施（如及时更新系统或安装补丁程序等）防范操作系统软件漏洞风险、电子设备中的病毒等。</p>
            <p>（3）妥善保管借记卡、手机及与借记卡快捷支付业务相关的重要身份证件等资料，不交给其他人保管，不放置在不安全场所。</p>
            <p>（4）避免使用容易被其他人猜出或破解的密码（如：采用生日、电话号码等与个人信息相关的信息或具有规律性的数字或字母组合作为快捷支付业务密码），避免设置与其他用途相同的密码（如：采用电子邮件密码作为借记卡快捷支付密码）。客户应定期或不定期更新其密码。</p>
            <p>（5） 应经常关注账户内资金变化，如发现任何通过中国银行借记卡快捷支付从事的未经客户许可的操作及交易，客户应马上与支付机构或中国银行取得联系。</p>
            <p>10.借记卡快捷支付业务资金清算可能出现异常及差错交易，银行及合作支付机构将对异常及差错交易及时进行纠正处理。银行及合作支付机构将根据客户要求提供交易记录的时间和方式等信息。</p> +
            <p>11.客户不得利用中国银行快捷支付业务进行虚假交易、洗钱、诈骗、恐怖融资等行为，且有义务配合中国银行进行相关调查，一旦客户拒绝配合进行相关调查或中国银行认为客户存在或涉嫌虚假交易、洗钱或任何其他非法活动、欺诈或违反诚信原则的行为、或违反本协议约定的，中国银行有权采取以下一种、多种或全部措施：</p><p>（1）暂停或终止提供本协议项下快捷支付业务服务；</p><p>（2）终止本协议；</p><p>（3）取消客户的用卡资格。</p>
            <p>12.客户同意中国银行就快捷支付业务的开展，向支付机构提供客户留存在中国银行的户名（如与支付机构约定不校验姓名则去掉）、银行卡号、证件号码（如与支付机构约定不校验证件号码则去掉）、手机号码等信息。</p>
            <p>13.客户可以登录中国银行官方网站撤销本人名下指定借记卡与支付机构账户关联的快捷支付功能。</p>
            <p>14.中国银行系统升级、业务变化、收费变更，或根据业务发展需要修改本协议，中国银行将提前进行公告。若客户有异议，有权选择撤销相关服务，若客户选择继续接受该服务的,视为客户同意并接受相关业务或协议按变更或修改后的内容执行。双方同意，本协议所称公告均指在中国银行网点、中国银行网站【http://www.boc.cn】、电子银行渠道等进行公告。</p> +
            <p>15.选择打勾的客户，视同已通读上述条款，对条款的内容及其后果已充分理解，对所有内容均无异议。</p>
            <p>16.本协议在客户于支付机构提供的页面服务协议栏位打勾并提交后生效。本协议的生效、履行及解释均使用中华人民共和国法律（为本协议之目的，不含中国香港、澳门、台湾地区法律）。本协议项下的争议向中国银行所在地人民法院起诉。</p>
            <p>合同使用说明：本合同为个人支付客户使用中国银行借记卡快捷支付服务时与中国银行签订的服务协议。中国银行借记卡快捷支付合作支付机构应在支付客户签约中国银行借记卡快捷支付服务时向客户展示该协议，客户阅签该协议后方可签约借记卡快捷支付服务。</p></div></div></div>
    <footer class="tc mt10">
        <p class="c999 fs12">本服务由易宝支付提供</p>
        <p class="c999 fs12">客服电话：400-500-800 客服公众号：yeepay007</p>
    </footer>
    <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
    
  </div>
</template>

<script>
import VerifyInfo from '@/components/verifyInfo'
import EnterCardNo from '@/components/enterCardNo'
import BindCardSuccess from '@/components/bindCardSuccess'
import BindCardFailed from '@/components/bindCardFailed'
import QueryPage from '@/components/queryPage'
let ERR_OK = 'success'; //返回成功码
export default {
  name: 'addBankCard',
  data () {
    return {
      isAuthorization: this.isAuthorization ? this.isAuthorization:'', //是否授权成功
      protocol:false, //服务协议显示
      bankprotocol:false, //中国银行服务协议显示
      isBindCardPay:'', //绑卡支付标识
      orderInfo:false,  //绑卡支付订单信息
      // tradeTime: '',  //交易时间
      productName:'',//产品名称
      companyName:'',//商户名称
      orderId: '',   //订单编号
      amount: '',//金额
      isShowBankTip:false, //银行弹框
      isShowMask: false,  //遮罩
      bankCardInfo:[], //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardType":"信用卡","number":"2218"}]
      isCreditCard:true,  //信用卡
      placeholder: '短信验证码',
      required: false, //验证码是否为必填项
      verifyName: '', //验证码是否有name
      smsCode: '', //验证码值
      check: true,//同意协议
      nopOrderId: '',  //接口返回的数据
      requestFlowId: '' , //接口返回的数据
      contextPath:contextPath  //图片路径
    }
  },
  watch:{
    smsCode(){
      if(this.smsCode.length > 6){
          this.smsCode = this.smsCode.substring(0,6);
      }
    }
  },
  created() {
    // this.tradeTime = tradeTime;
    this.orderId = orderId;
    this.amount = amount;
    this.productName = productName;
    this.companyName = companyName;
    this.isBindCardPay = token //绑卡支付标识
    var bindCardInfo = JSON.parse(localStorage.getItem("bindCardInfo"));
    if(bindCardInfo == null || !bindCardInfo || bindCardInfo == 'undefined'){
      //缓存不存在的情况
        alert("未能获取到绑卡信息");
        return false;
    }
    if(bindCardInfo.cardType == '2'){
      this.isCreditCard = true;  //信用卡
    }else{
      this.isCreditCard = false;
    }
    this.bankCardInfo.push(bindCardInfo)
    // localStorage.removeItem("bindCardInfo")//什么时候清除   留下来？？？？？？？？？  
  },
  mounted() { 
    User.form.validate("#form");
  },
  methods:{
    showProtocal() {
      // 服务协议
      var self = this;
      self.isShowMask = true;
      self.protocol = true;
    },
    showBankProtocal() {
      // 服务协议 
      var self = this;
      self.isShowMask = true;
      self.bankprotocol = true;
    },
    close() {
      var self = this;
      self.isShowMask = false;
      self.protocol = false;
      self.check = true;
    },
    closeBank() {
      var self = this;
      self.isShowMask = false;
      self.bankprotocol = false;
      self.check = true;
    },
    getVerifyCode(e) {  //获取验证码
      var self = this;
      self.required = false;//验证码是否为必填项
      self.verifyName='';//验证码是否有name
      self.$nextTick(function () {   //特别重要，dom更新完毕再执行如下操作
        var result = User.form.autoValidate("#form");
        var target = e.target;
        if(result){
          self.placeholder ='正在获取验证码';
          var cardInfo = User.form.serialize("#form");
          var cardno = JSON.parse(localStorage.getItem("addItem")).cardno;
          if(cardno == null || cardno == 'undefined'){
              alert("未拿到卡号");
              return false;
          }
          User.form.disabledSubmit("#getCode");   //按钮不可点击
          if(self.isAuthorization == ''){   //首次鉴权绑卡
            var param = User.convertJson(cardInfo,bindCardMerchantRequest);
            param.token = token;
            param.cardno = cardno;
            self.$http.get(Request.url+'/bindCard/authBindCard',{params: param}).then((res) => {
              User.form.enabledSubmit("#getCode");   //按钮可点击
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              if(data.bizStatus == ERR_OK){  //成功
                User.autoValidateCode(target);//获取验证码开始倒计时
                self.placeholder ='请输入验证码';
                self.nopOrderId = data.authBindRequest.nopOrderId;
                self.requestFlowId = data.authBindRequest.requestFlowId;
                localStorage.nopOrderId = self.nopOrderId;
                localStorage.requestFlowId = self.requestFlowId;
                self.isAuthorization ='fail'; 
              }else{
                self.isShowMask = true;
                Tip.titleContentBtn('提示',data.errormsg,function(){
                  dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                    self.isShowMask = false;
                    dom.querySelector("#tip1").classList.add("none");
                  },false);
                })
                if(data.smsStatus == 'smsSendError'){  //可以重新发短信
                  self.isAuthorization ='fail';
                }else{
                  self.isAuthorization ='';//鉴权绑卡失败
                }
              }
            });
          }else if(self.isAuthorization == 'fail'){
            var param = bindCardMerchantRequest;
            param.token = token;
            param.nopOrderId = self.nopOrderId;
            param.requestFlowId = self.requestFlowId;
            self.$http.get(Request.url+'/bindCard/authBindCardSMS',{params: param}).then((res) => {
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              User.form.enabledSubmit("#getCode");   //按钮可点击
              if(data.bizStatus == ERR_OK){  //成功
                  User.autoValidateCode(target);//获取验证码开始倒计时
              }else{
                self.isShowMask = true;
                var smsStatus = data.smsStatus;
                Tip.titleContentBtn('提示',data.errormsg,function(){
                  dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                    self.isShowMask = false;
                    dom.querySelector("#tip1").classList.add("none");
                  },false);
                })
                if(smsStatus == 'smsSendError'){  //可以重新发短信
                    User.autoValidateCode(target);//获取验证码开始倒计时
                }else if(smsStatus == 'smsNotPermitError'){  //不可以重新发短信
                  self.isShowMask = true;
                  Tip.titleContentBtn('提示','不允许再次发短验了哦',function(){
                    dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                      self.isShowMask = false;
                      dom.querySelector("#tip1").classList.add("none");
                    },false);
                  })
                  return false;
                } 
                return false;
              }
            });
          }
        }
      })
    },
    surePay() {  //确认支付
      var self = this;
      self.required = true;//验证码是否为必填项
      self.verifyName = 'smsCode';//验证码是否有name smsCode
      self.$nextTick(function () {   //特别重要，dom更新完毕再执行如下操作
        var result = User.form.autoValidate("#form");
        if(result){
          User.form.disabledSubmit("#submit");
          var cardInfo = User.form.serialize("#form");
          var param = User.convertJson(cardInfo,bindCardMerchantRequest);  //bindCardMerchantRequest:入口取到的object对象
          param.token = token;
          param.smsCode = self.smsCode;
          param.nopOrderId = self.nopOrderId;
          param.requestFlowId = self.requestFlowId;
            self.$http.get(Request.url+'/bindCard/authBindCardConfirm',{params: param}).then((res) => {
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              User.form.enabledSubmit("#submit");
              switch(data.bizStatus){
                case 'bindPaySuccess': //绑卡支付成功
                  location.assign(self.contextPath+"/wap/query/result?token="+token);
                break;
                case 'bindPayFail':  //绑卡支付失败
                  if(data.smsStatus == "smsVerifyError" || data.smsStatus == "smsInputError" || data.smsStatus == "smsNotGetError"){
                      //smsNotGetError  没调短验接口直接确认返回的字段
                      //smsVerifyError   smsInputError都是调了短验接口返回的字段
                      self.isShowMask = true;
                      Tip.titleContentBtn('提示',data.errormsg,function(){
                        dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                          self.isShowMask = false;
                          dom.querySelector("#tip1").classList.add("none");
                        },false);
                      })
                      self.isAuthorization == 'fail';
                  }else{
                    var errormsg = data.errormsg;
                    var errorcode = data.errorcode;
                    var needRepay = data.needRepay;
                    if(needRepay){
                      location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg+"&needRepay="+needRepay);
                    }else{
                      location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg);
                    }
                  }
                break;
                case 'bindPayNeedItem': //绑卡支付 补充项
                  var cardNumber = JSON.parse(localStorage.getItem("addItem"));
                  if(cardNumber == null || cardNumber == undefined){
                      alert('卡号获取失败');
                      return false;
                  }
                  var needSurportDTO = data.needBankCardItem.needSurportDTO;  //是否补充项
                   ////
                  var cardInfo2  = {};  //返回的里列表信息
                  for(var item in data.needBankCardItem){
                    if(typeof data.needBankCardItem[item] == 'string'){
                      cardInfo2[item] = data.needBankCardItem[item];
                    }
                  }
                  ////
                  var smsType = data.smsType;
                  var addItem = User.convertJson(cardNumber,cardInfo2); 
                  if(smsType){  //需要短信验证
                    localStorage.smsType = data.smsType;
                  }
                  localStorage.bindId = data.authBindConfirm.bindId;
                  localStorage.setItem("addItem",JSON.stringify(addItem));
                  localStorage.setItem("needSurportDTO",JSON.stringify(needSurportDTO));
                  self.$router.push({name:'VerifyInfo', path: '/VerifyInfo', component:VerifyInfo})
                break;
                case 'bindPayUnknown':  //支付未知
                   location.assign(self.contextPath+"/query_index.vm");
                break;
                case 'bindCardFail':  //绑卡失败
                  if(data.smsStatus == "smsVerifyError" || data.smsStatus == "smsInputError" || data.smsStatus == "smsNotGetError"){
                      //smsNotGetError  没调短验接口直接确认返回的字段
                      //smsVerifyError   smsInputError都是调了短验接口返回的字段
                      self.isShowMask = true;
                      Tip.titleContentBtn('提示',data.errormsg,function(){
                        dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                          self.isShowMask = false;
                          dom.querySelector("#tip1").classList.add("none");
                        },false);
                      })
                      self.isAuthorization == 'fail';
                  }else{
                    self.$router.push({name:'BindCardFailed', path:'/BindCardFailed', component:BindCardFailed});//绑卡失败
                  }
                break;
                case 'bindCardSuccess':  //绑卡成功
                  localStorage.frontCallBackUrl = data.frontCallBackUrl;
                  self.$router.push({name:'BindCardSuccess', path:'/BindCardSuccess', component:BindCardSuccess});//绑卡成功
                break;
                case 'bindCardUnKnown':  //绑卡未知
                  self.$router.push({name:'QueryPage', path:'/QueryPage', component:QueryPage});//查询页
                break;
              }
            });
        }
      })
    },
    showBankTip() { //显示银行弹框
      var self = this;
      self.isShowMask = true;
      self.isShowBankTip = true;
    },
    goTo() {
      this.$router.push({ name:'EnterCardNo', path: '/', component: EnterCardNo})
    },
    hiddenBankTip() {  //隐藏银行弹框
      var self = this;
      self.isShowMask = false;
      self.isShowBankTip = false;
    },
    noSubmit(event){
      event.preventDefault();
      return false;
    }
  }
}
</script>

