<template>
  <div class="verifyInfo">
    <div class="wrapper b-transparent">
      <form class="bt bgfff fs16 mt10" id="form" name="form" @submit="noSubmit($event)">
        <label v-if="this.needSurportDTO.cardnoIsNeed">
          <div class="info li pr">
            <mark class="dis-inline text c333 pl12">银行卡号</mark>
            <input class="required input-text" name="cardno" encrypt="true" type="text" maxlength="23" placeholder="请输入银行卡号">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.ownerIsNeed">
          <div class="info li pr">
            <mark class="dis-inline text c333 pl12">姓&nbsp;&nbsp;&nbsp;名</mark>
            <input class="required input-text" name="owner" encrypt="true" type="text" placeholder="您的真实姓名">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.idnoIsNeed">
          <div class="info bt li pr">
            <mark class="dis-inline text c333 pl12">身份证号码</mark>
            <input class="required input-text" name="idno" encrypt="true" maxlength="22" type="text" placeholder="真实证件号">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.phoneNoIsNeed">
          <div class="info bt li pr">
            <mark class="dis-inline text c333 pl12">手机号</mark>
            <input class="required input-text" name="phoneNo"  encrypt="true" maxlength="11" type="text" placeholder="银行预留手机号">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <div v-if="this.isCreditCard" class="creditCard">
          <label v-if="this.needSurportDTO.avlidDateIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12">有效期</mark>
              <input class="required input-text" name="avlidDate" encrypt="true" type="text" maxlength="4" placeholder="月/年(如09/15 输入0915)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.needSurportDTO.cvvIsNeed">
            <div class="info li pr bt">
              <mark class="dis-inline text c333 pl12">C V V 2</mark>
              <input class="required input-text" name="cvv" type="text" encrypt="true" maxlength="3" placeholder="卡背面后三位数字">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
        </div>
          <label v-if="this.needSurportDTO.idCardTypeIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12">证件类型</mark>
              <input class="required input-text" name="idCardType" encrypt="true" placeholder="证件类型">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.needSurportDTO.bankPWDIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12">取款密码</mark>
              <input class="required input-text" name="bankPWD" maxlength="6" encrypt="true" type="password" placeholder="请输入取款密码">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.verify">
            <div class="info bt li pr verifyBox">
              <mark class="dis-inline text c333 pl12">验证码</mark>
              <input class="input-text verifyCode" :class="{required:this.required}" maxlength="6" :name="this.verifyName" type="text" :placeholder="placeholder">
              <button class="fs14 tc getCode pa" id="getCode" @click="getVerifyCode" type="button">获取验证码</button>
            </div>
          </label>
      </form>
      <!-- <p class="fs12 c999 pl12 mt10">验证码已发送至****{{phoneEndNo}}</p> -->
      <div class="pl12 pr12">
        <button class="mt30 w" :class="{dis:check}" id="submit" @click="surePay" :disabled="check" type="button">确认支付</button>
      </div>
    </div>
    <!-- <footer class="tc fs14 c999">{{cardType}}需要完善补充项</footer> -->
    <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
  </div>
</template>

<script>
let ERR_OK = 'success'; //返回成功码
// import PaySuccess from '@/components/paySuccess.vue'
export default {
  name: 'verifyInfo',
  data () {
    return {
      isShowBankTip:false, //银行弹框
      isShowMask: false,  //遮罩
      // phoneEndNo: '您的手机', //手机尾号
      isCreditCard: true, //是否是信用卡
      placeholder: '短信验证码',
      required: false, //验证码是否为必填项
      verifyName: '', //验证码是否有name
      addItem: null, //缓存取信息
      needSurportDTO: {
        "cardnoIsNeed":'',
        "ownerIsNeed":'',
        "idnoIsNeed":'',
        "phoneNoIsNeed":'',
        "cvvIsNeed":'',
        "avlidDateIsNeed":'',
        "idCardTypeIsNeed":'',
        "bankPWDIsNeed":''
      }, //缓存取补充项是否需要显示
      // idCardType: '',//补充项 证件类型
      idCardTypeVal: '',//补充项 证件类型
      verify:'',//是否需要短信
      bankPWDVal: '',//补充项 密码,
      bindId:'',//补充项 绑卡Id 
      check:true //按钮是否可点击
    }
  },
  created() {
    var self = this;
    self.addItem = JSON.parse(localStorage.getItem("addItem"));//获取补充项
    if(localStorage.getItem("needSurportDTO") != 'undefined'){
      self.needSurportDTO = JSON.parse(localStorage.getItem("needSurportDTO"));//获取补充项是否显示标志
    }
    self.bindId = localStorage.bindId; //补充项 绑卡Id
    self.verify = localStorage.smsType;//是否需要短信验证
    if(self.addItem == null || self.addItem == undefined){
        alert('补充项信息获取失败');
        return false;
    }
    if(!self.verify){
      self.check = false;
    }
  },
  mounted(){
    var self = this;
  },
  methods:{
    getVerifyCode(e) {  //获取验证码
      var self = this;
      self.required = false;//验证码是否为必填项
      self.verifyName='';//验证码是否有name
      self.$nextTick(function () {   //特别重要，dom更新完毕再执行如下操作
        var result = User.form.autoValidate("#form");
        var target = e.target;
        if(result){
          self.placeholder ='正在获取验证码'; 
          var formItem = User.form.serialize("#form");
          var param = User.convertJson(self.addItem,formItem); 
          param.token = token;
          param.bindId = this.bindId;
          param.smsType = 'YEEPAY';
          User.form.disabledSubmit("#getCode");
          self.$http.get(Request.url+'/wap/ajax/bindSmsSend',{params: param}).then((res) => {
            User.form.enabledSubmit("#getCode");
            var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
            self.placeholder ='请输入验证码';
            if(data.status != 'success'){
              self.isShowMask = true;
              Tip.titleContentBtn('提示',data.msg,function(){
                dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                  self.isShowMask = false;
                  dom.querySelector("#tip1").classList.add("none");
                },false);
              })
            }
            User.autoValidateCode(target);//获取验证码开始倒计时
            self.check= false;
          });
        }
      })
    },
    surePay() {  //确认支付
      var self = this;
      self.required = true;//验证码是否为必填项
      self.verifyName = 'verifycode';//验证码是否有nameverifyCode
      self.$nextTick(function () {   //特别重要，dom更新完毕再执行如下操作
        var result = User.form.autoValidate("#form");
        if(result){
          // var param = self.addItem;
          var formItem = User.form.serialize("#form");
          var param = User.convertJson(self.addItem,formItem); 
          param.token = token;
          param.bindId = self.bindId;
          param.firstBindCardPay = true;
          User.form.disabledSubmit("#submit");
          self.$http.get(Request.url+'/wap/ajax/pay/bind',{params: param}).then((res) => {
            User.form.enabledSubmit("#submit");
            var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
            if(data.bizStatus == ERR_OK){
              location.assign(Request.url+"/wap/query/result?token="+token);
            }else{
              var errormsg = data.errormsg;
              var errorcode = data.errorcode;
              var needRepay = data.needRepay;
              if(needRepay){
                location.assign(Request.url+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg+"&needRepay="+needRepay);
              }else{
                location.assign(Request.url+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg);
              }
            }
          });
        }
      })
    },
    noSubmit(event){
      event.preventDefault();
      return false;
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.wrapper{
  .dis{background: #ccc;}
  min-height: 94vh;
  #form{
    label{
      display: block;
      height:44px;
      mark{
       display: inline-block;
        width: 73px;
        height: 100%;
        line-height: 44px;
        background-color: transparent;
        text-align: justify;
        overflow: hidden;
      }
      mark:after {
          content: " ";
          display: inline-block;
          width: 100%
      }
    }
  }
  .info{
    width:100%;
    height:44px;
    line-height: 44px;
    &.verifyBox{
      .errMsg{
        right:88px;
      }
    }
    .text{
      width:72px;
      margin-right: 14px;
    }
    .input-text{
      width:calc(100%-40px);
      line-height: 40px;
      vertical-align: top;
      &.verifyCode{height: 44px;}
    }
    .resendMessage{
      background: #f0f0f0;
      color:#666;
      height:32px;
      line-height: 32px;
      top:4px;
      right:12px;
    }
    .getCode{
      width:92px;
      height:32px;
      line-height: 32px;
      background: #2eb944;
      color:#fff;
      top:4px;
      right:12px;
      &:active{
        background: #2ea009;
      }
    }
  }
}
</style>



