<template>
  <div class="bindCardSuccess">
    <span class="icon successLogo">&#xe613;</span>
    <p class="mt20 c333 fs20">绑卡成功</p>
    <div class="pl12 pr12">
      <button class="mt30 w" v-if="frontCallBackUrl" @click="toIndex" type="button">返回商户</button>
      <!-- <router-link tag="button" class="mt30 w" :to="{name:'EnterCardNo'}">返回商户</router-link> -->
      <!-- <router-view></router-view> -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'bindCardSuccess',
  data () {
    return {
      contextPath: contextPath,  //路径
      frontCallBackUrl:''  //链接地址
    }
  },
  created(){
    this.frontCallBackUrl = localStorage.frontCallBackUrl;
    localStorage.frontCallBackUrl= '';//清缓存，防止新请求的商户没传这个参数，结果缓存中存在这次的缓存
     
  },
  methods:{
    toIndex() {
      location.assign(this.frontCallBackUrl);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.bindCardSuccess{
  padding-top: 50px;
  text-align: center;
  .successLogo{
    color:#2eb944;
    font-size:54px;
  }
}
</style>



